                <h2><span class="firm"><strong>Enhanced Oil Recovery </strong></span></h2>
                <p>Enhanced oil recovery (EOR), also known as tertiary recovery, is a process for extracting oil that
                    has not already been retrieved through the primary or secondary oil recovery techniques.<br>

                    Although the primary and secondary recovery techniques rely on the pressure differential between the
                    surface and the underground well, enhanced oil recovery functions by altering the chemical
                    composition of the oil itself in order to make it easier to extract.&nbsp;</p>
                <div class="end"></div>
            </div>
            <div class="columnContent">

                <h2><span class="firm"><strong>Why We Need EOR Database</strong></span></h2>
                <p>Enhanced Oil Recovery (EOR) provides a means to progressively expand hydrocarbon exploitation through
                    scientific advancements.
                    EOR methods have been proved to mobilize, and induce the flow of oil to producing wells in primary,
                    secondary, and tertiary recovery stages.
                    This work involves the construction of an EOR database based on numerous reported EOR projects. The
                    database lists reservoir rock and fluid
                    properties in addition to other project attributes related to oil saturations, incremental recovery,
                    demography, and project start date.
                    The database provides a profile of worldwide EOR trends. Finally, this paper illustrates the
                    relationship of EOR project distribution to key
                    reservoir characteristics. The database can be a vital tool for formulating field development plans,
                    ensuring project viability, and
                    clarifying research development priorities.&nbsp;</p>
